<template>
  <div class="index-page">
    <div class="banner">
      <img src="@/assets/images/banner.png" alt="">
    </div>
    <div class="index-content-box">
      <div class="index-content">
        <div class="info-text">
          本次募捐总额为 <strong>{{maxTotal}}</strong> USDT，捐赠者可获 <strong>1 : {{ 1 / (price/10000)}}</strong> 得 <strong>FLD</strong>（FreeLandDao）治理代币。<br>
募集资金将用于组织发展与运动，去中心化公开使用。<br>
治理代币可参与去中心化组织治理投票、行使原始革命股权、享受革命成果增值收益。

        </div>
        <div class="content flex-middle">
          <div class="process-box">
            <!-- <img class="process-left" src="@/assets/images/process_left.png" alt=""> -->
            <div class="process">
              <div class="bar-box">
                <img class="bar-bg" src="@/assets/images/bar_bg.png" alt="">
                <div class="bar-div">
                   <img class="bar" :style="{'width': `${Number(total * 100/maxTotal).toFixed(2)}%`}" src="@/assets/images/bar.png" alt="">
                </div>
              </div>
            </div>
            <div class="process-text">
              <div class="title">进度: {{Number(total * 100/maxTotal).toFixed(2)}}%</div>
               <div class="text">筹集 {{total}} USDT 总数为 {{maxTotal}} USDT</div>
            </div>
          </div>
          <div class="button-box">
              <img class="button" @click="isShow = true" src="@/assets/images/button.png" alt="">
              <div class="text">
                参与运动可获取<span>白纸运动徽章碎片</span><br>
集齐徽章可兑换去中心化资金池活动奖励。<br>
具体细则将在捐赠完成后进行公布。
              </div>
            </div>
        </div>
      </div>
    </div>
    <div class="footer">
      FREELANDDAO
    </div>
     <van-popup
    v-model:show="isShow"
    :style="{ width: '600px', background: 'none' }"
    :close-on-click-overlay="true"
  >
    <div class="popup-box">
      <div class="flex-between">
        <span></span>
        <van-icon name="cross" size="22" color="#333" @click="isShow = false"/>
      </div>
      <div class="invitations">
        <p class="invitations-title">募捐</p>
       <van-field type="Number" placeholder="请输入募捐数量" v-model="usdtAmount" >
            <template #button>
          USDT
        </template>
        </van-field>
       <div class="tip">USDT余额: {{ usdtBalance }}USDT</div>
      </div>
      <van-button loading-text="授权中..." v-if="Number(allowance) == 0 || Number(allowance) < Number(usdtAmount)" :loading="loading" type="primary" block size="large" @click="approve">授权USDT</van-button>
      <van-button loading-text="募捐中..." v-else type="primary" :loading="loading" block size="large"  @click="ido">募捐</van-button>
    </div>
  </van-popup>
  </div>
</template>
<script>
import { reactive, toRefs, computed, watch } from 'vue'
import { useStore } from "vuex"
import config from "@/utils/config"
import tokenAbi from "@/utils/abi"
import idoAbi from "@/utils/idoAbi"
import { MaxUint256 } from "@ethersproject/constants"
import BigNumber from 'bignumber.js'
import { Toast } from 'vant'

export default {
  components: {
  },
  setup () {
    const store = useStore()

    // const route = useRoute
    const data = reactive({

      address: computed(() => store.state.account),
      web3: computed(() => store.state.provider),
      idoContract: null,
      usdtContract: null,
      allowance: '0',
      usdtBalance: '0',
      total: '0',
      usdtAmount: '',
      maxTotal: '0',
      price: 10000,
      loading: false,
      isShow: false,
      initContract: () => {
        data.idoContract = new data.web3.eth.Contract(idoAbi, config.idoAddress)
        data.usdtContract = new data.web3.eth.Contract(tokenAbi, config.usdtAddress)
        data.initData()
      },
      initData: () => {
        data.getAllowance()
        data.getBalance()
        data.getIdoInfo()
      },
      getIdoInfo: async() => {
        let total = await data.idoContract.methods.total().call()
        let maxTotal = await data.idoContract.methods.maxTotal().call()
        let price =  await data.idoContract.methods.price().call()
        data.total = new BigNumber(total).dividedBy(10**18).toFixed(0)
        data.maxTotal = new BigNumber(maxTotal).dividedBy(10**18).toFixed(0)
        data.price =  price
      },
      getBalance: async () => {
        let usdtBalance = await data.usdtContract.methods.balanceOf(data.address).call()
        data.usdtBalance = new BigNumber(usdtBalance).dividedBy(10**18).toFixed(6,0)
      },
      getAllowance:async () => {
        let result = await data.usdtContract.methods.allowance(data.address, config.idoAddress).call();
        data.allowance =  new BigNumber(result).dividedBy(10**18).toFixed();
      },
      approve: async () => {
        try {
          data.loading = true
          let gas = await data.usdtContract.methods.approve(config.idoAddress, MaxUint256).estimateGas({from: data.address})
          await data.usdtContract.methods.approve(config.idoAddress, MaxUint256).send({ from: data.address, gas: gas });
          data.getAllowance()
          data.loading = false
        } catch (error) {
          data.loading = false
          console.log(error)
        }
      },
  
      ido: async () => {
        if(!data.usdtAmount) {
          Toast('请输入募捐数量')
          return
        }
        if (Number(data.usdtBalance) < Number(data.usdtAmount)) {
          Toast('USDT余额不足')
          return
        }
        let amount = new BigNumber(data.usdtAmount).multipliedBy(10**18).toFixed()
         data.loading = true
        try {
          await data.idoContract.methods.ido(amount).send({ from: data.address });
           data.loading = false
           data.isShow = false
          Toast('募捐成功')
          data.initData()
        } catch (error) {
           data.loading = false
          console.log(error)
          Toast(JSON.stringify(error))
        }
      },

    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>

<style lang="less" scoped>
.banner{
  overflow: hidden;
  img{
    margin: 0;
    display: block;
    position: relative;
    left: 50%;
    margin-left: -960px;
  }
}
.index-content-box{
  background: #171939;
  margin: 0 auto;
  .index-content{
    width: 1200px;
    margin: 0 auto;
    padding: 30px;
    box-sizing: border-box;
  }
}
.info-text{
  text-align: center;
  font-size: 14px;
  line-height: 24px;
  color: #fff;
  margin-bottom: 20px;
  strong{
    color: #ffc960;
    font-size: 18px;
  }
}
.bar-box{
  // margin-left: -69px;
  position: relative;

  z-index: 0;
  &::before{
    position: absolute;
    content: '';
    width: 218px;
    height: 221px;
    background: url('~@/assets/images/process_left.png') no-repeat center center;
    top: -74px;
    left: -144px;
    z-index: 2;
  }
}
.process-box{
  padding-left: 144px;
}
.process-left{
  position: relative;
  z-index: 1;
}
.bar-div{
    position: absolute;
    left: 36px;
    top: 11px;
     right: 10px;
    bottom: 16px;
    overflow: hidden;
    border-radius: 0 40px 40px 0;
    // box-sizing: border-box;
    // padding: 11px;
}
.bar{
  display: block;
  height: 100%;
  width: 0%;
}
.content{
  justify-content: center;
  .button-box{
    margin-left: 60px;
    .text{
      height: 80px;
      margin-top: 5px;
       font-size: 14px;
      color: #6e7fff;
      line-height: 24px;
      text-align: center;
      span{
        color: #ffc960;
      }
    }
  }
}
.process-text{
  text-align: center;
  height: 80px;
  margin-top: 5px;
  margin-left: 144px;
  .title{
    color: #ffc960;
    margin-bottom: 10px;
  }
  .text {
    font-size: 14px;
    color: #6e7fff;
  }
}
.footer{
  height: 90px;
  text-align: center;
  font-size: 18px;
  font-weight: bold;
  color: #ffc960;
  line-height: 90px;
}
.popup-box {
  padding: 10px 20px 20px;
  background: #fff;
  border-radius: 10px;
  .receive {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .invitations {
    margin: 0 0 23px;
    .invitations-title {
      font-size: 18px;
      color: #333;
      font-weight: bold;
      margin-top: 10px;
      margin-bottom: 13px;
    }
   
  }
  .tip{
    margin-top: 10px;
    color: #666;
  }
}
</style>
